body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eee;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading{
  margin: 0px auto;
  margin-top: 100px;
  height: auto;
  width: 80%;
  /* background-color: #ddd; */
  display: grid;
  grid-template-columns:repeat(2, auto);
  grid-row-gap: 100px;
}

.loading article{
  margin: 0px auto;
  background-color: #fff;
  font-size: 12px;
  width: 500px;
  height: 250px;
  box-shadow: 0px 3px 13px #000;
  border-radius: 6px;
  position: relative;
}

.loading article img{
  position: absolute;
  top: -70px;
  left: 20px;
  width: 130px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.507);
}

.loading article h3{
  position: absolute;
  top: 20px;
  right: 0px;
  width: 330px;
  font-size: 17px;
}

.year{
  position: absolute;
  top: 5px;
  right: 30px;
  width: 300px;
  font-size: 14px;
  font-weight: bold;
}

.summary{
  position: absolute;
  top: 110px;
  right: 30px;
  width: 300px;
  height: 150px;
  font-size: 12px;
  text-overflow: ellipsis;
  /* overflow: hidden; */
}

.genres{
  /* display: flex; */
  list-style: none;
  position: absolute;
  top: 70px;
  left: 170px;
  padding-inline-start: 0px;
  padding-block-start: 0px;
  color: #777;
  width: 300px;
  /* overflow: hidden; */
}

.genres li{
  display: inline-block;
  margin-right: 5px;
  font-size: 13px;
}

.genres li::after{
  content: " / ";
}

.genres li:last-child::after{
  display: none;
}

.move_img{
  /* animation-name: move;
  animation-duration: 2s;
  animation-iteration-count: infinite; */
  width: 35px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes move {
  0%{
    transform: scale(1);
  }
  50%{
    transform: scale(1.1);
    
  }
  100%{
    transform: scale(1);

  }
}
